	module.exports = {"data":{"post":{"html":"<p>In case you wonder why developers are so difficult to work with, <a href=\"http://lumma.de/eintrag.php?id=3248\">Nico heard this</a> in our office in Cologne:</p>\n<blockquote><p>\n  I reject <a href=\"http://www.rubyonrails.org/\">Ruby on Rails</a> for reasons of conscience.\n</p></blockquote>\n<p>Try explaining that to a client when your project is late and over budget. \"We could have used a better framework, but our development team thought it was immoral.\"</p>","fields":{"title":"Conscientious Objector","date":"07 Feb 2007","url":"https://papascott.de/2007-02-07-conscientious-objector/"}}},"pathContext":{"slug":"/2007-02-07-conscientious-objector/","prev":{"fields":{"title":"Attitude","date":"06 Feb 2007","slug":"/2007-02-06-attitude/"}},"next":{"fields":{"title":"Wrong Passport","date":"07 Feb 2007","slug":"/2007-02-07-wrong-passport/"}}}}

